/**
 * @name Sidebar component
 */

import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import NavItem from 'react-bootstrap/lib/NavItem';
import Nav from 'react-bootstrap/lib/Nav';
// import Button from 'react-bootstrap/lib/Button';
import Glyphicon from 'react-bootstrap/lib/Glyphicon';
// import MenuItem from 'react-bootstrap/lib/MenuItem';
// import MenuItem from 'react-bootstrap/lib/MenuItem';
// import NavDropdown from 'react-bootstrap/lib/NavDropdown';
import Collapse from 'react-bootstrap/lib/Collapse';
import LinkContainer from 'react-router-bootstrap/lib/LinkContainer';
import Icon from 'react-icons-kit';
import { primitiveDot } from 'react-icons-kit/oct/primitiveDot';
import { horizontalCenter } from 'react-icons-kit';

import { Menu } from '../../menu';
import './Sidebar.css';


/**
 * Vertical aligned icon in menu item
 * @todo Move to independent component (or kill it)
 */
const NavItemIconed =
    horizontalCenter(
        props => <LinkContainer to={props.link}><NavItem>{props.children}</NavItem></LinkContainer>
    );


/**
 * SidebarSubMenu
 * 
 * @param {boolean} collapsed
 * @param {object} item
 * @returns 
 */
function SidebarSubMenu({ collapsed, children, id }) {
    return (
        <Collapse in={collapsed}>
            <Nav className="nav-stacked left-submenu" id={id}>
                {children && children.map(child => {
                    return (
                        <SidebarSubMenuItem link={child.link} title={child.title} key={child.id} />
                    );
                })}
            </Nav>
        </Collapse>
    );
}


/**
 * SidebarSubMenuItem
 * 
 * @param {string} link
 * @param {string} title
 * @returns
 */
function SidebarSubMenuItem({ link, title }) {
    return (
        <NavItemIconed link={link}>
            <Icon size={14} icon={primitiveDot} />{title}
        </NavItemIconed>
    );
}

SidebarSubMenuItem.propTypes = {
    link: PropTypes.string,
    title: PropTypes.string
};

SidebarSubMenuItem.defaultProps = {
    link: '{no_link}',
    title: '{no_title}',
};


/**
 * SidebarMenuItem
 * Class - because we need expanded state
 * @class SidebarMenuItem
 * @extends {Component}
 */
class SidebarMenuItem extends PureComponent {
    constructor(props) {
        super(props)
        this.state = {};
    }

    static propTypes = {
        id: PropTypes.string.isRequired,
        title: PropTypes.string,
        icon: PropTypes.string,
        link: PropTypes.string,
        children: PropTypes.array
    }

    static defaultProps = {
        id: '',
        title: '{no_title}',
        icon: 'menu-right',
        link: '',
        children: []
    }
    static shouldComponentUpdate = function (nextProps, nextState) {
        console.log(nextProps);
        console.log(nextState);

        return false;
    }
    hToggleSubMenu = (e) => {
        let key = this.props.id;
        console.log('key: ', key);
        let newState = this.state['menu-' + key] === undefined ? true : !this.state['menu-' + key];
        this.setState({
            ['menu-' + key]: newState
        });
    }

    render() {
        const {
            id, title, icon, link, children

        } = this.props;
        return (
            <li>
                <Link to={link} onClick={this.hToggleSubMenu}>
                    <Glyphicon glyph={icon} /> {title}
                    {children.length > 0 && <span className="caret arrow"></span>}
                </Link>
                <SidebarSubMenu collapsed={this.state['menu-' + id]} children={children} id={id} />
            </li>
        );
    }
}

/**
 * Left sidebar menu
 *
 * @class Sidebar
 * @extends {Component}
 */
function Sidebar() {
    return (
        <Nav className="nav-pills nav-stacked left-menu" id="sidebar">
            {Menu && Menu.map(item => {
                return (
                    <SidebarMenuItem
                        id={item.id}
                        title={item.title}
                        icon={item.icon}
                        children={item.children}
                        link={item.link}
                        key={item.id} />
                );
            })}
        </Nav>
    );
}

Sidebar.shouldComponentUpdate = function (nextProps, nextState) {
    console.log(nextProps);
    console.log(nextState);
    return false;
}

Sidebar.SidebarSubMenuItem = function (nextProps, nextState) {
    console.log(nextProps);
    console.log(nextState);
    return false;
}

Sidebar.SidebarSubMenu = function (nextProps, nextState) {
    console.log(nextProps);
    console.log(nextState);
    return false;
}

export default Sidebar;


