import React, { Component } from 'react';
import { Nav, NavItem, Navbar, NavDropdown, MenuItem, Glyphicon, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

// import './Topbar.css';

class Topbar extends Component {
    toggleSidebar() {
        document.getElementById('sidebar').classList.toggle('active');
    }
    render() {
        return <Navbar fluid >
            <Navbar.Header>
                <Navbar.Brand>
                    <Link to="/">Nextbi</Link>
                </Navbar.Brand>
                <Navbar.Toggle />
            </Navbar.Header>
            <Navbar.Collapse>
                <Navbar.Form pullLeft>
                    <Button bsStyle="primary" onClick={() => this.toggleSidebar()}><Glyphicon glyph="menu-hamburger" /></Button>
                </Navbar.Form>
                <Nav>
                    <NavDropdown eventKey={1} title="Item 1" id="dropdown1">
                        <MenuItem eventKey={1.1} href="#">Item 1.1</MenuItem>
                    </NavDropdown>
                    <NavItem eventKey={2} to="/users-list">Item 2</NavItem>
                    <NavItem eventKey={3}>Item 3</NavItem>
                </Nav>
            </Navbar.Collapse>
        </Navbar>;
    }
}

export default Topbar;


